/* begin global stuff */
body {
  padding: 1rem;
  font-family: sans-serif;
}

a:hover {
  background-color: red;
}
/* end global stuff */

/* Begin Nav Bar Styling */
nav ul {
  list-style-type:none;
  margin: 0;
  padding: 0rem;
}

nav li {
  display: inline;
  padding-right: 1rem;
}

/* begin dropdown menu */

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}
/* end drop down menu */

.current {
  color: green;
  background-color: blue;
  font-size: 2em;
  margin-top: 0.67em;
  margin-bottom: 0.67em;
  margin-left: 0;
  margin-right: 0;
  font-weight: bold;
}
/* End Nav Bar Styling */

h1 {
    display: block;
    font-size: 2em;
    margin-top: 0.67em;
    margin-bottom: 0.67em;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;
}
